<template>
  <div class="wrapper" v-show="isShowSearch">
    <div class="header">
      <div class="icon-wrapper">
        <i class="back" @click="back"></i>
      </div>
      <div class="search-box">
        <search-box ref="searchBox" @query="onQueryChange" @focus="focus" placeholder="请输入商品名称"></search-box>
      </div>
      <div class="text-wrapper" @click="back">
        <span>取消</span>
      </div>
    </div>
    <div class="content-wrapper">
      <history
        v-show="!productList.length && searchHistory.length"
        @query="onQueryChange"
        @clear="clearHistory"
        :list="searchHistory"
      >
      </history>
      <scroll class="content" v-show="productList.length">
        <div class="list">
          <good-item
            ref="goodItem"
            class="item"
            v-for="(item,index) in productList"
            :key="item.productItemId"
            :item="item"
            @count="calc"
          >
          </good-item>
        </div>
      </scroll>
      <div class="settle-wrapper" v-show="total">
        <settle @submit="submit" :price="price" :total="total"></settle>
      </div>
    </div>
    <confirm ref="confirm" @confirm="clearSearchHistory" :message="confirmMessage"></confirm>
  </div>
</template>

<script type="text/ecmascript-6">
  import GoodItem from 'base/good-item/good-item'
  import Scroll from 'base/scroll/scroll'
  import SearchBox from 'base/search-box/search-box'
  import Confirm from 'base/confirm/confirm'
  import Settle from 'base/settle/settle'
  import History from 'components/purchase/history'
  import {mapActions, mapGetters} from 'vuex'

  import {ERR_CODE} from 'api/config'
  import {getProductList,addOrUpdateShoppingCart} from 'api/purchase'

  export default {
    data () {
      return {
        query: '',
        productList: [],
        selectedGoods: {},
        total: 0,
        price: 0,
        confirmMessage: ''
      }
    },
    props: {
      isShowSearch: {
        type: Boolean,
        default: false
      }
    },
    computed: {
      currentRoute(){
        return this.$router.currentRoute.path
      },
      ...mapGetters([
        'searchHistory'
      ])
    },
    methods: {
      back() {
        // 保证下次进来的时候，数据还是初始数据 --- BEGIN
        this.$refs.searchBox.setQuery('')
        this.productList = []
        this.selectedGoods = {}
        this.total = 0
        this.price = 0
        // 保证下次进来的时候，数据还是初始数据 --- END
        this.$emit('hide')
      },
      focus() {
        //dosomething
      },
      showConfirm() {
        this.$refs.confirm.show()
      },
      clearHistory() {
        this.confirmMessage = '确认删除搜索历史记录么？'
        this.showConfirm()
      },
      onQueryChange(newQuery) {
        let goodItems = this.$refs.goodItem
        // 重置count
        if(goodItems && goodItems.length){
          for(let i = 0; i < goodItems.length; i++){
            goodItems[i].setCount(0)
          }
        }
        this.query = newQuery
        this.$refs.searchBox.setQuery(newQuery)
      },
      _getProductList() {
        getProductList().then((res) => {
          if(res.errCode === ERR_CODE){
            this.productList = res.data
          }
        })
      },
      // 计算数量和价格
      calc(item){
        addOrUpdateShoppingCart({
          'productItemId':item.productItemId,
          'count':item.count
        }).then((res) => {
          if(res.errCode === ERR_CODE){
            this.selectedGoods[item.productItemId] = item
            let tempTotal = 0
            let tempPrice = 0
            for(let key in this.selectedGoods){
              let value = this.selectedGoods[key]
              tempTotal += value.count
              tempPrice += value.price * value.count
            }
            this.total = tempTotal
            this.price = tempPrice
            this.item = item
          }
        })
      },
      // 跳转到购物车模块
      submit(){
        this.setShoppingCart(this.selectedGoods)
        this.$router.push({
          path: `${this.currentRoute}/shoppingcart`
        })
      },
      ...mapActions([
        'setShoppingCart',
        'setSearchHistory',
        'clearSearchHistory'
      ])
    },
    watch: {
      query(newQuery) {
        this.setSearchHistory(newQuery)
        this._getProductList()
      }
    },
    components: {
      SearchBox,
      History,
      Confirm,
      Settle,
      GoodItem,
      Scroll
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .wrapper
    background $color-background
    position absolute
    left 0
    right 0
    top 0
    bottom 0
    z-index 1
    .header
      position fixed
      height 44px
      top 0
      left 0
      right 0
      background $color-header-background
      display flex
      justify-content center
      align-items center
      .icon-wrapper
        width 50px
        height 100%
        .back
          position absolute
          background url("~common/image/back.png") no-repeat
          background-size contain
          width 8px
          height 15px
          left 15px
          top 15px
          &:before
            position absolute
            left -10px
            right -10px
            top -10px
            bottom -10px
            content ''
      .search-box
        display flex
        flex 1
      .text-wrapper
        width 50px
        height 100%
        display flex
        align-items center
        justify-content center
        color #3d3d3d
        font-size $font-size-medium
    .content-wrapper
      position absolute
      left 0
      right 0
      top 44px
      bottom 0
      background $color-background
      display flex
      justify-content center
      .content
        width 100%
        overflow hidden
        margin-top 10px
        .list
          .item
            height 110px
      .settle-wrapper
        position fixed
        left 0
        right 0
        bottom 0
        height 45px

</style>



