<template>
  <transition name="slide">
    <div class="wrapper">
      <div class="header">
        <div class="icon-wrapper">
          <i class="back" @click="back"></i>
        </div>
        <div class="search-box">
          <search-box @query="onQueryChange" @focus="showSearch" ref="searchBox"></search-box>
        </div>
        <div class="icon-wrapper">
          <i class="icon-category" @click="showCategory"></i>
        </div>
      </div>
      <div class="content-wrapper">
        <scroll class="content">
          <div class="list">
            <good-item
              ref="goodItem"
              class="item"
              v-for="(item,index) in productList"
              :key="item.productItemId"
              :item="item"
              @count="calc"
            >
            </good-item>
          </div>
        </scroll>
        <div class="settle-wrapper" v-show="total">
          <settle @submit="submit" :price="price" :total="total"></settle>
        </div>
      </div>
      <category v-show="isShowCategory" @hide="hideCategory" @query="onQueryChange"></category>
      <search :isShowSearch="isShowSearch" @hide="hideSearch"></search>
      <router-view></router-view>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
  import GoodItem from 'base/good-item/good-item'
  import Scroll from 'base/scroll/scroll'
  import SearchBox from 'base/search-box/search-box'
  import Settle from 'base/settle/settle'
  import Search from 'components/purchase/search'
  import Category from 'components/purchase/category'
  import {mapActions,mapGetters} from 'vuex'

  import {ERR_CODE} from 'api/config'
  import {getProductList,addOrUpdateShoppingCart} from 'api/purchase'

  export default {
    data () {
      return {
        query: '',
        productList: [],
        isShowSearch: false,
        isShowCategory: false,
        selectedGoods: {},
        total: 0,
        price: 0
      }
    },
    created () {
      this._getProductList()
    },
    computed: {
      currentRoute(){
        return this.$router.currentRoute.path
      },
      ...mapGetters([
        'searchHistory'
      ])
    },
    methods: {
      back() {
        this.$router.back()
      },
      showSearch() {
        this.isShowSearch = true
      },
      hideSearch() {
        this.isShowSearch = false
      },
      showCategory() {
        this.isShowCategory = true
      },
      hideCategory() {
        this.isShowCategory = false
      },
      onQueryChange(newQuery) {
        let goodItems = this.$refs.goodItem
        // 重置count
        if(goodItems){
          for(let i = 0; i < goodItems.length; i++){
            goodItems[i].setCount(0)
          }
        }
        this.query = newQuery
        this.$refs.searchBox.setQuery(newQuery)
      },
      _getProductList() {
        getProductList().then((res) => {
          if(res.errCode === ERR_CODE){
            this.productList = res.data
          }
        })
      },
      // 计算数量和价格
      calc(item){
        addOrUpdateShoppingCart({
          'productItemId':item.productItemId,
          'count':item.count
        }).then((res) => {
          if(res.errCode === ERR_CODE){
            this.selectedGoods[item.productItemId] = item
            let tempTotal = 0
            let tempPrice = 0
            for(let key in this.selectedGoods){
              let value = this.selectedGoods[key]
              tempTotal += value.count
              tempPrice += value.price * value.count
            }
            this.total = tempTotal
            this.price = tempPrice
            this.item = item
          }
        })
      },
      // 跳转到购物车模块
      submit(){
        this.setShoppingCart(this.selectedGoods)
        this.$router.push({
          path: `${this.currentRoute}/shoppingcart`
        })
      },
      ...mapActions([
        'setShoppingCart',
        'setSearchHistory'
      ])
    },
    watch: {
      query(newQuery){
        this.setSearchHistory(newQuery)
        this._getProductList()
      }
    },
    components: {
      SearchBox,
      Search,
      Settle,
      GoodItem,
      Scroll,
      Category
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .slide-enter-active,.slide-leave-active
    transition all 0.3s
  .slide-enter,.slide-leave-to
    transform translate3d(100%,0,0)

  .wrapper
    background $color-background
    position absolute
    left 0
    right 0
    top 0
    bottom 0
    z-index 1
    .header
      position fixed
      height 44px
      top 0
      left 0
      right 0
      background $color-header-background
      display flex
      justify-content center
      align-items center
      .icon-wrapper
        width 50px
        height 100%
        .back
          position absolute
          background url("~common/image/back.png") no-repeat
          background-size contain
          width 8px
          height 15px
          left 15px
          top 15px
          &:before
            position absolute
            left -10px
            right -10px
            top -10px
            bottom -10px
            content ''
      .search-box
        display flex
        flex 1
      .icon-wrapper
        .icon-category
          position absolute
          background url("~common/image/purchase/category.png") no-repeat
          background-size contain
          width 14px
          height 13px
          right 15px
          top 16px
          &:before
            position absolute
            left -10px
            right -10px
            top -10px
            bottom -10px
            content ''
    .content-wrapper
      position absolute
      left 0
      right 0
      top 44px
      bottom 0
      background $color-background
      display flex
      justify-content center
      .content
        width 100%
        overflow hidden
        margin-top 10px
        .list
          .item
            height 110px
      .settle-wrapper
        position fixed
        left 0
        right 0
        bottom 0
        height 45px
</style>



