<template>
  <div class="history-wrapper">
      <div class="tilte">
        <span>{{title}}</span>
        <i @click="clearHistory"></i>
      </div>
      <ul class="word-wrapper">
        <li class="item" v-for="(keyword,index) in list" @click="selectItem(keyword)">
            {{keyword}}
        </li>
      </ul>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    data () {
      return {

      }
    },
    props: {
        list: {
            type: Array,
            default: []
        },
        title: {
            type: String,
            default: '历史记录'
        }
    },
    methods: {
      selectItem(keyword) {
        this.$emit('query', keyword)
      },
      clearHistory(){
        this.$emit('clear')
      }
    },
    components: {

    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
    @import "~common/stylus/variable"

    .history-wrapper
      width 100%
      height 100%
      color $color-background
      .tilte
        display flex
        height 40px
        align-items center
        justify-content space-between
        margin-left 10px
        margin-right 10px
        color #999
        font-size $font-size-small-x
        i
          background url("~base/search-box/delete.png") no-repeat
          background-size contain
          width 14px
          height 14px
          position relative
          &:before
            position absolute
            left -10px
            top -10px
            right -10px
            bottom -10px
            content ''
      .word-wrapper
        background #fff
        display flex
        flex-wrap wrap
        box-sizing border-box
        padding-left 15px
        padding-right 15px
        .item
          margin-top 20px
          margin-right 10px
          background #f0f0f0
          color #4c4c4c
          font-size $font-size-medium
          min-width 60px
          height 30px
          display flex
          justify-content center
          align-items center
          &:last-child
            margin-bottom 20px

</style>



